.birthdate-form {
  width: 420px;
  text-align: center;
  margin: 5px auto;
}

.birthdate-form label {
  display: block;
  font-size: 12px;
  margin-bottom: 5px;
  font-style: italic;
}

.birthdate-form input {
  margin-right: 10px;
}

.birthdate-form input[type='submit'] {
  background: #FF5252;
  color: #fff;
  border: none;
  height: 24px;
  border-radius: 3px;
  cursor: pointer;
}

.life {
  margin: 20px 0 0 0;
  padding: 30px 30px 0 30px;
  position: relative;
}

.yearwrapper {
  position: relative;
  width: 420px;
  margin: 0 auto;
}

.graphkey {
  position: absolute;
  font-size: 1.5rem;
  color: #607D8B;
  cursor: default;
}

.graphkey.x {
  top: -40px;
  left: 30px;
}

.graphkey.y {
  -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-font-smoothing: antialiased;
  top: 10px;
  left: -60px;
}

.label {
  position: absolute;
  left: 0px;
  top: -3px;
  font-size: 12px;
  line-height: 12px;
  color: #455A64;
}

.year {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

.week {
  height: 5px;
  width: 5px;
  background-color: transparent;
  margin: 1px;
}

.active--false {
  background-color: #CFD8DC;
}

.active--true {
  background-color: #FF5252;
}




